import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps } from 'summer';
import { ItemFatorRisco } from '../../../../models/dtos/mfp/itemFatorRisco';
import FatorRiscoAmbienteTrabalhoIS from '../../../components/mfp/fatorRiscoAmbienteTrabalho/FatorRiscoAmbienteTrabalhoIS';

const ItemFatorRiscoEditView: FC<DetailEditViewFormProps<ItemFatorRisco>> = props => {
  const formProps = useForm<ItemFatorRisco>({
    defaultValues: { ...props.selectedItem },
    mode: 'onBlur',
  });

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <FatorRiscoAmbienteTrabalhoIS
            formProps={{ ...formProps, validation: { required: true } }}
            label="Fator Risco"
            placeholder="Selecione um Fator de Risco"
            id="fatorRiscoAmbienteTrabalho"
            isPrependLabel={true}
            inputSearchProps={{ selectProps: { isMulti: false } }}
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default ItemFatorRiscoEditView;
