import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import {
  ColumnsProps,
  DefaultSortProps,
  DetailManagerForm,
  DetailManagerFormProps,
  EditForm,
  EditViewProps,
  getSelectedItem,
  InputGroupInline,
  InputType,
  OrderType,
  WindowComponent,
} from 'summer';
import { AmbienteTrabalho, CLASS_NAME } from '../../../../models/dtos/mfp/ambienteTrabalho';
import { LOCAL_AMBIENTE_TRABALHO_ENUM_VALUES } from '../../../../models/enumerated/ger/localAmbienteTrabalhoEnum';
import EmpresaIS from '../../../components/ger/empresaIS/EmpresaIS';
import ItemFatorRiscoEditView from './ItemFatorRiscoEditView';

interface GrupoEventoEditViewProps extends EditViewProps<AmbienteTrabalho> {}

const width: number = 120;

const GrupoEventoEditView: FC<GrupoEventoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<AmbienteTrabalho>({
    defaultValues: selectedItem,
    mode: 'onBlur',
  });

  const defaultSortProps: DefaultSortProps[] = [
    {
      dataField: 'empresa',
      order: OrderType.ASC,
    },
  ];

  const columns: ColumnsProps[] = [
    {
      dataField: 'fatorRiscoAmbienteTrabalho.codigo',
      text: 'Código',
    },
    {
      dataField: 'fatorRiscoAmbienteTrabalho.descricao',
      text: 'Descrição',
    },
  ];

  const itemFatorRiscoFormProps: DetailManagerFormProps = {
    columns,
    detailView: ItemFatorRiscoEditView,
    formProps,
    propName: 'itensFatorRisco',
    title: 'Fatores de Risco',
  };

  return (
    <WindowComponent title="Manutenção de Ambiente de Trabalho">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline
          id="localAmbienteTrabalho"
          label="Ambiente de Trabalho"
          formProps={{ ...formProps, validation: { required: true } }}
          isPrependLabel={true}
          placeholder="Informe o Ambiente de Trabalho"
          inputDropDownProps={{ optionsProvider: LOCAL_AMBIENTE_TRABALHO_ENUM_VALUES }}
          type={InputType.DROP_DOWN}
        />
        <EmpresaIS
          formProps={{ ...formProps, validation: { required: true } }}
          label="Empresa"
          placeholder="Selecione uma Empresa"
          id="empresa"
          isPrependLabel={true}
          inputSearchProps={{ size: 'xl', selectProps: { isMulti: false } }}
        />
        <InputGroupInline
          formProps={{ ...formProps, validation: { required: true, maxLength: 999 } }}
          id="descricao"
          isPrependLabel={true}
          label="Descrição"
          labelWidth={width}
          placeholder="Informe a Descrição."
          type={InputType.TEXT_AREA}
        />
        <DetailManagerForm
          {...props}
          defaultSortDirection={OrderType.DESC}
          defaultSorted={defaultSortProps}
          {...itemFatorRiscoFormProps}
          keyField="rowIndex"
        />
      </EditForm>
    </WindowComponent>
  );
};

export default GrupoEventoEditView;
